<template>
    <div class="pt-10">
      <ModalAddPlayer />
      <div v-if="$auth.is" class="mb-2 flex justify-end">
        <Button @click="$noc('#modalAddNewPlayer').modal('show')">Add</Button>
      </div>
      <Vue3EasyDataTable :headers="headers" :items="players" sort-by="power" sort-type="desc">
  
        <template #item-id_player="{ id_player }">
          
          <template v-if="is_edit">
            <input v-model="stat_update.id_player" class="relative w-auto h-10 px-4 text-sm placeholder-transparent transition-all border rounded outline-none focus-visible:outline-none peer border-slate-200 text-slate-500 autofill:bg-white focus:border-emerald-500 focus:outline-none disabled:cursor-default disabled:bg-white disabled:text-slate-400" >
          </template>
          <template v-else>
            <p>{{ id_player }}</p>
          </template>
  
        </template>
        <template #item-name="{ name }">
          
          <template v-if="is_edit">
            <input v-model="stat_update.name" class="relative w-auto h-10 px-4 text-sm placeholder-transparent transition-all border rounded outline-none focus-visible:outline-none peer border-slate-200 text-slate-500 autofill:bg-white focus:border-emerald-500 focus:outline-none disabled:cursor-default disabled:bg-white disabled:text-slate-400" >
          </template>
          <template v-else>
            <p>{{ name }}</p>
          </template>
  
        </template>
        <template #item-furnice_level="{ furnice_level }">
          
          <template v-if="is_edit">
            <label class="flex items-center space-x-2 mb-2 mt-2 cursor-pointer">
                  <input v-model="stat_update.is_fc" type="checkbox" true-value="1" false-value="0" class="w-5 h-5 text-emerald-500 border-gray-300 rounded focus:ring-emerald-500">
                  <span class="text-gray-700">Crystal Furnace</span> 
            </label>
            <input type="number" v-model="stat_update.furnice_level" class="relative w-auto h-10 px-4 text-sm placeholder-transparent transition-all border rounded outline-none focus-visible:outline-none peer border-slate-200 text-slate-500 autofill:bg-white focus:border-emerald-500 focus:outline-none disabled:cursor-default disabled:bg-white disabled:text-slate-400" >
          </template>
          <template v-else>
            <p>{{ furnice_level }}</p>
          </template>
  
        </template>
        <template #item-power="{ power }">
          
          <template v-if="is_edit">
            <input type="number" v-model="stat_update.power" class="relative w-auto h-10 px-4 text-sm placeholder-transparent transition-all border rounded outline-none focus-visible:outline-none peer border-slate-200 text-slate-500 autofill:bg-white focus:border-emerald-500 focus:outline-none disabled:cursor-default disabled:bg-white disabled:text-slate-400" >
          </template>
          <template v-else>
            <p>{{ power }} M</p>
          </template>
  
        </template>
  
        <template v-if="$auth.is" #item-actions="{ id }">
  
          <template v-if="is_edit">
            <button
              @click="updatePlayer(id)"
              class="bg-blue-500 text-white px-3 py-1 rounded hover:bg-blue-700 mr-2"
            >
              SAVE
            </button>
            <button
              @click="is_edit = false"
              class="bg-red-500 text-white px-3 py-1 rounded hover:bg-red-700 mr-2"
            >
              CANCEL
            </button>
          </template>
          <template v-else>
            <button
              @click="[is_edit = !is_edit, handleUpdatePlayer(id)]"
              class="bg-green-500 text-white px-3 py-1 rounded hover:bg-green-700 mr-2"
            >
              EDIT
            </button>
            <button
              @click="deletePlayer(id)"
              class="bg-red-500 text-white px-3 py-1 rounded hover:bg-red-700"
            >
              DELETE
            </button>
          </template>
        </template>
        
      </Vue3EasyDataTable>
    </div>
  </template>
  
  <script>
  // @ is an alias to /src
  import Button from '@/components/noc_ui/Button.vue'
  import Vue3EasyDataTable from 'vue3-easy-data-table';
  import 'vue3-easy-data-table/dist/style.css';
  
  import HelloWorld from '@/components/HelloWorld.vue'
  import ModalAddPlayer from '@/components/ModalAddPlayer.vue'
  import Input from '@/components/noc_ui/Input.vue'
  
  export default {
    name: 'HomeView',
    components: {
      HelloWorld,
      Vue3EasyDataTable,
      Button,
      ModalAddPlayer,
      Input
    },
    data() {
      return {
        is_edit: false,
        stat_update: {
          id_player: '',
          name: '',
          is_fc: 0,
          furnice_level: 0,
          power: 0
        },
        headers: [
            { text: "ID", value: "id_player" },
            { text: "NAME", value: "name"},
            { text: "LEVEL", value: "furnice_level"},
            { text: "POWER", value: "power"},
            { text: "ACTION", value: "actions", sortable: false }
        ],
        players: [
          {},
        ]
      }
    },
    created(){
      this.getPlayers()
    },
    methods: {
      handleUpdatePlayer(id){
        this.stat_update = this.players.find(player => player.id === id)
      },
      async getPlayers(){
        const { data, error } = await this.$supabase.from("members").select("*")
  
        if (error) {
          this.$Swal.fire("", "Error", "error");
        } else {
          this.players = data
        }
      },
      async deletePlayer(id) {
        const { error } = await this.$supabase.from("members").delete().eq("id", id)
        if (error) {
          this.$Swal.fire("", "Error", "error")
          return
        }
  
        this.$Swal.fire("", "Deleted successfully", "success")
        this.getPlayers()
      },
      async updatePlayer(id) {
  
        const { error } = await this.$supabase.from("members").update(this.stat_update).eq("id", id)
        if (error) {
          this.$Swal.fire("", "Error", "error")
          return
        }
  
        this.$Swal.fire("", "Deleted successfully", "success")
        this.is_edit = false
        this.stat_update = {
          id_player: '',
          name: '',
          is_fc: 0,
          furnice_level: 0,
          power: 0
        }
        this.getPlayers()
      }
    },
    mounted() {
      
    }
  }
  </script>
  
  <style>
  #app {
    font-family: Avenir, Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
  }
  
  nav {
    padding: 30px;
  }
  
  nav a {
    font-weight: bold;
    color: #2c3e50;
  }
  
  nav a.router-link-exact-active {
    color: #42b983;
  }
  </style>
  